body {
  background-color: rgba(33, 40, 177, 0.842);
}

.statistics {
  width: 500px;

  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
}
.title {
  margin-top: 0;
  margin-bottom: 10px;
  color: rgb(70, 191, 221);

  display: flex;
  justify-content: center;
}
.stats {
  list-style: none;

  display: flex;

  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}
.item {
  width: 100px;
  height: 100px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
