body {
  background-color: rgba(32, 33, 53, 0.842);
}

.transactionHistory {
  width: 900px;
  margin: 0 auto;
  box-shadow: 0 1px 4px rgb(114, 102, 102),
    -23px 0 20px -23px rgba(0, 0, 0, 0.8), 23px 0 20px -23px rgba(0, 0, 0, 0.8),
    0 0 40px rgba(0, 0, 0, 0.1) inset;
}
tr {
  height: 40px;
}
th {
  font-size: 25px;
  background-color: #00bcd5;
  color: white;
}
td {
  text-align: center;
  font-size: 20px;

  color: #7c8489;
}
